.button {
  font-size: 12px;
  text-decoration: none;
  padding: 7px 12px;
  font-weight: 500;
  background: none;
  border: 2px solid blue;
  color: blue;
  text-align: center;
}

.secondary {
  @extend .button;

  width: 120px;
  background-color: blue;
  color: white;
  font-weight: 700;
  font-size: .8em;
  box-shadow: 0 5px 20px -5px rgba(#818181, .9);
}